import { defineComponent, computed } from 'vue';
import { useStore } from '@/store';
import pageSearch from '@/components/page-search';
import PageContent from '@/components/page-content';
import PageModal from '@/components/page-modal';
import { searchFormConfig } from './config/search.config';
import { contentTableConfig } from './config/content.config';
import { modalConfig } from './config/modal.config';
import { usePageSearch } from '@/hooks/use-page-search';
import { usePageModal } from '@/hooks/use-page-modal';
export default defineComponent({
  name: 'department',
  components: {
    pageSearch,
    PageContent,
    PageModal
  },

  setup() {
    // 1.search对应hook
    const [pageContentRef, handleResetClick, handleQueryClick] = usePageSearch(); // 2.modal对应hook

    const [pageModalRef, defaultInfo, handleNewData, handleEditData] = usePageModal(); // 3.动态添加select的options,利用computed变成响应式

    const store = useStore();
    const modalConfigRef = computed(() => {
      const departmentItem = modalConfig.formItems.find(item => item.field === 'parentId');
      departmentItem.options = store.state.entireDepartment.map(item => {
        return {
          title: item.name,
          value: item.id
        };
      }); // const departmentItem = modalConfig.formItems.find(
      //   (item: any) => item.filed === 'parentId'
      // )
      // departmentItem!.options = store.state.entireDepartment.map((item) => {
      //   return { label: item.name, value: item.id }
      // })

      return modalConfig;
    });
    return {
      searchFormConfig,
      pageContentRef,
      handleResetClick,
      handleQueryClick,
      contentTableConfig,
      pageModalRef,
      defaultInfo,
      handleNewData,
      handleEditData,
      modalConfig,
      modalConfigRef
    };
  }

});